.loader {
  width: 100vw;
  height: 100vh;
  background: #f7f7f7;
  justify-content: center;
  align-items: center;
  transition: opacity .75s, visibility .75s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.loader--hidden {
  opacity: 0;
  visibility: hidden;
}

.loader:after {
  content: "";
  width: 75px;
  height: 75px;
  border: 15px solid #545454;
  border-top-color: #ff6b08;
  border-radius: 50%;
  animation: loading .75s infinite;
}

